body {
  font-family: "Lato", sans-serif;
}

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 90vh;
}

.content-wrap {
  flex: 1 1;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.timazon {
  color: #4da58b;
  position: absolute;
  margin-top: 55px;
  padding-left: 18px;
  font-size: 1.6rem;
}
.cart {
  position: relative;
}

.form-control:focus {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.sign-in-button {
  margin-top: 15px;
  width: 99%;
  border: 1px solid black;
  padding: 5px;
  border-radius: 3px;
  background: linear-gradient(to right, #68a2dc 0%, #62aaf3 50%, #5a9cde 99%);
}

.sign-in-button:hover {
  background: #62aaf3;
  cursor: pointer;
}

.account-question {
  font-size: 0.8rem;
  font-weight: 400;
  margin-top: 20px;
  margin-bottom: 5px;
}

.register {
  margin: 0;
  font-size: 0.8rem;
  font-weight: 400;
  color: #0e7ded;
}

.register:hover,
.guest:hover {
  color: #ed620e;
  cursor: pointer;
}

.guest {
  font-size: 0.8rem;
  font-weight: 400;
  color: #0e7ded;
  margin: 10px 0 0 0;
  text-decoration: underline;
  width: 100px;
}

.loading-animation-container {
  top: 50%;
  left: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.loading {
  border: 6px solid #ccc;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border-top-color: #4da58b;
  border-left-color: #4da58b;
  -webkit-animation: spin 1s infinite ease-in-out;
          animation: spin 1s infinite ease-in-out;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
  }
}

