.category-bar {
  margin-top: 100px;
  background-color: #f8f9fa;
  border-bottom: 1px solid #cfd4d9;
  position: fixed;
  width: 100%;
  z-index: 101;
}

.intro-list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0;
}

.intro-list-item {
  display: inline-block;
  margin: 8px 20px 8px 20px;
}

.category-item {
  border: none;
  background: none;
  margin-bottom: 0;
  position: relative;
}

.category-item::after {
  content: "";
  position: absolute;
  width: 100%;
  background: #000;
  height: 0.15rem;
  left: 0;
  bottom: 0;
  background: #4da58b;
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.category-item:hover::after {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

.category-dropdown-icon {
  font-size: 0.8rem;
}

