.review-information-rating {
  color: #f3a842;
  margin: 0 0 5px 3px;
}

.review-information-verified {
  color: #b60000;
  margin: 0 0 15px 3px;
}

.review-information-description {
  margin: 0 0 5px 3px;
}

.review-information-buy-again {
  margin: 0 0 5px 3px;
}

.buy-again-answer {
  font-weight: 600;
}

.modal.left .product-view-modal-dialog {
  position: fixed;
  right: 0;
  margin: auto;
  width: 320px;
  height: 100%;
  -webkit-transform: translate3d(0%, 0, 0);
  transform: translate3d(0%, 0, 0);
}

.modal.left .product-view-modal-content {
  height: 100%;
  overflow-y: auto;
}

.product-view-modal-header {
  height: 50px;
}

.product-view-close-x {
  font-size: 2rem;
  font-weight: 600;
}

.modal.right .product-view-modal-body {
  padding: 15px 15px 80px;
}

.modal-backdrop.show {
  opacity: 0.2;
}

.product-added-modal-body {
  display: flex;
  flex-direction: column;
}

.quickshop-product-title {
  align-self: start;
}

.product-view-added-product-information {
  display: flex;
  align-content: center;
  font-size: 0.9rem;
  margin-bottom: 25px;
}

.product-view-added-product-image {
  height: 170px;
  width: 170px;
  align-self: center;
}

.product-view-added-modal-title {
  font-size: 1.4rem;
  font-weight: bold;
}

.product-view-added-product-title {
  margin: 0 0 10px 10px;
}

.product-view-added-product-price {
  margin: 0 0 10px 10px;
  font-weight: bold;
  font-size: 0.9rem;
}

.product-view-added-product-size,
.product-view-added-product-quantity {
  margin: 0 0 0 10px;
  font-size: 0.9rem;
}

.product-view-added-keep-shopping-container {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 25px;
  background-color: #4da58b;
  border-radius: 5px;
}

.product-view-added-keep-shopping-container:hover,
.product-view-added-view-cart-container:hover {
  cursor: pointer;
  background-color: #55b498;
}

.product-view-added-keep-shopping {
  margin: 0;
}

.product-view-added-view-cart-container {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0 25px 0;
  background-color: #4da58b;
  border-radius: 5px;
  transition: 0.3s background-color;
}

.product-view-added-view-cart {
  font-size: 1rem;
  margin-bottom: 0;
}

.product-view-added-cart-icon {
  font-size: 1.4rem;
  margin: 0 0 2px 10px;
}

.product-view-added-wishlist-icon {
  font-size: 1.1rem;
  margin: 0 0 2px 5px;
}

.product-view-added-also-like-container {
  margin-top: 20px;
}

.product-view-added-also-like-title {
  font-weight: bold;
}

.product-view-added-also-like-products {
  text-align: center;
  margin-top: 50px;
}

.product-view-added-also-like-product {
  display: inline-block;
}

.product-view-added-also-like-image {
  height: 120px;
  width: 120px;
  margin: 4px 4px 4px 4px;
}

@media (max-width: 600px) {
  .product-information-checkout-container {
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    width: 100%;
  }

  .product-view-information-container {
    width: 100%;
    height: 100%;
  }

  .product-view-checkout-box {
    width: 100%;
    height: 100%;
    margin: 15px 0 0 0;
  }

  .product-view-image-container {
    width: 100%;
    height: 100%;
  }

  .product-view-review-content {
    flex-direction: column;
  }

  .review-give-your-review {
    width: 100%;
  }
  .customer-reviews {
    margin-left: 0;
  }
  .modal.left .product-view-modal-dialog {
    width: 100%;
  }
}

@media (max-width: 500px) {
  .product-view-container {
    margin-top: 0;
  }
}

@media (max-width: 767px) and (min-width: 600px) {
  .product-information-checkout-container {
    margin: 20px 0;
    justify-content: space-evenly;
    width: 100%;
  }

  .product-view-information-container {
    width: 65%;
  }

  .product-view-checkout-box {
    width: 35%;
  }

  .product-view-image-container {
    align-self: center;
    width: 50%;
    height: 50%;
  }
}

@media (max-width: 767px) {
  .product-view-product-info {
    flex-direction: column;
    margin-bottom: 20px;
  }
}

.product-review-container {
  margin: 80px 60px 100px 60px;
}

.product-review-title {
  font-weight: bold;
  font-size: 1.6rem;
  margin: 0 0 30px 0;
}

.product-review-product {
  display: flex;
  justify-content: flex-start;
}

.review-product-img {
  height: 100px;
  width: 80px;
  margin: 0 20px 0 0;
}

.review-product-title {
  margin: 10px 0 0 0;
  font-size: 1.3rem;
}

.review-product-review {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0 0 20px 0;
}

.review-product-review-title {
  font-size: 1.3rem;
  font-weight: 600;
}

.review-product-review-stars {
  font-size: 2.2rem;
}

.review-product-review-stars:hover {
  cursor: pointer;
}

.star-empty {
  margin-right: 5px;
  color: #f3a842;
}

.star-full {
  color: #f3a842;
  margin-right: 5px;
}

.review-product-review-clear {
  background: none;
  border: none;
}

.review-product-review-clear:hover {
  text-decoration: underline;
}

.review-product-buy-again-title {
  font-size: 1.3rem;
  font-weight: 600;
  margin: 0 0 20px 0;
}

.review-product-message-title {
  font-size: 1.3rem;
  font-weight: 600;
  margin: 0 0 20px 0;
}

.review-product-message-input {
  width: 80%;
  padding: 10px 0 150px 5px;
}

.review-product-name-title {
  font-size: 1.3rem;
  font-weight: 600;
  margin: 20px 0 3px 0;
}

.review-product-appear {
  font-size: 0.9rem;
}

.review-product-user-info {
  display: flex;
  align-items: center;
  margin: 10px 0 0 0;
}

.review-product-profile {
  font-size: 4rem;
}

.review-product-profile-name {
  width: 72%;
}

.review-product-submit {
  background: none;
  border: 1px solid #4da58b;
  padding: 5px;
  width: 80px;
  position: absolute;
  right: 60px;
  transition: 0.3s;
}

.review-product-submit:hover {
  background-color: #4da58b;
}

.missing-rating {
  color: #b60000;
  font-weight: 600;
}

.review-submitted {
  margin-top: 60px;
  font-size: 1.3rem;
  color: #4da58b;
  font-weight: 600;
  text-align: center;
}

@media (max-width: 500px) {
  .product-review-container {
    margin-top: 0;
  }

  .review-product-message-input {
    width: 100%;
  }
}

.footer-container {
  margin-top: 20px;
  color: black;
  background-color: #4ca58a;
  padding-top: 0.5em;
  position: relative;
  bottom: 0;
  width: 100%;
}

.media-container {
  text-align: center;
}

.media {
  display: inline-block;
  margin-left: 25px;
  margin-right: 25px;
  margin-top: 10px;
}

.github,
.linkedIn,
.portfolio {
  height: 40px;
  width: 40px;
}

.portfolio {
  padding-top: 5px;
}

.social-media {
  display: block;
  text-decoration: none;
  color: #000;
  margin-top: 10px;
}

.social-media:hover {
  color: #000;
  text-decoration: underline;
}

.legal {
  text-align: center;
}

